.app {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  display: grid;
  grid-template-columns: 250px auto 300px;
  grid-template-rows: 100%;
  grid-column-gap: 3px;
  min-width: 800px;
  height: 100%;
  width: 100%;
  background: #202020; }

.app .workspace {
  width: 250px;
  height: 100%; }

.app .settings {
  height: 100%;
  overflow: auto; }

.app .renders {
  width: 300px;
  height: 100%;
  overflow: auto;
  overflow-x: hidden; }

.app .renders .view {
  width: 300px;
  height: 420px;
  background: white;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none; }



