.editor {
  background: #313131;
  color: #cccccc;
  padding: 15px 10px;
  min-height: calc(100% - 30px); }

.editor .groupsettings {
  margin-bottom: 20px; }

.editor .title {
  font-weight: bold;
  font-size: 25px;
  border-bottom: solid 1px #cccccc;
  padding-bottom: 3px;
  margin-bottom: 10px; }

.editor label {
  font-weight: bold;
  display: block;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none; }

.editor label img {
  max-height: 50px;
  display: block; }

.editor label:hover > input, .editor label:hover > textarea {
  border-bottom: solid 1px #777777; }

.editor label input, .editor label textarea {
  -webkit-user-select: contain;
     -moz-user-select: contain;
      -ms-user-select: contain;
          user-select: contain;
  resize: vertical;
  font-weight: normal;
  background: none;
  color: #cccccc;
  border: solid 1px transparent;
  padding: 2px 5px;
  display: block;
  width: calc(100% - 20px); }

.editor label input:focus, .editor label textarea:focus {
  outline: none;
  border-bottom: solid 1px #cccccc; }

.editor .save {
  font-weight: bold;
  display: block;
  margin-top: 10px;
  border: none;
  background: #cccccc;
  color: #000000;
  padding: 3px 10px;
  border-radius: 5px; }

.editor .save.unsaved {
  background: #711111;
  color: #cccccc; }

.editor .save:hover {
  background: #315131;
  color: #cccccc; }



